<template>
	<gui-page :isLoading="pageLoading">
		<view slot="gBody">
			<view class="p30"><jyf-parser :html="data" ref="article"></jyf-parser></view>
		</view>
	</gui-page>
</template>
<script>
	import jyfParser from "@/components/jyf-parser/jyf-parser";
	var _this;
	export default {
		data() {
			return { 
				pageLoading: false,
				data: null
			}
		},
		onLoad: function(){
			_this = this;
			_this.getRegAgreement();
		},
		methods: {
			getRegAgreement: function(){
				_this.pageLoading = true;
				uni.gRequest.post(
					'swoft/api/config/getRegAgreement',
					{
						data: {}
					}, 
					(res) => {
						_this.data = res.data;
					},
					(error) => {
						uni.gJS.msg('请求错误');
					},
					(res) => {
						_this.pageLoading = false;
					},
					true
				);
			}
		},
		components: {
			jyfParser
		}
	}
</script>
